/* HEADER STYLES */
.logo-wrapper {
  width: 100%;
  & > img {
    width: 100%;
    position: relative;
  }
}

.header {
  width: 100%;
  height: 400px;
  background-color: #000;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
  position: relative;
  overflow: hidden;

  & > .top-bar {
    position: relative;
    z-index: 1002;
  }

